	module.exports = {"data":{"markdownRemark":{"html":"<p><img src=\"https://danmaq.com/!/RAMTAN1GBRIMM/ram_face_ss.jpg\" alt=\"クリックすると詳細を閲覧できます。\"></p>\n<p><a href=\"/!/RAMTAN1GBRIMM/\">ちょっと謎な企画</a>を立ててみました。どう転げるかは気分次第。</p>\n<p>東方以外のイラストだけど超久々に描いた気がする。確か一昨年の始め位に悟史＆沙都子描いたのが最後だったかな。</p>\n<h2>例大祭行ってきました！……客としてね</h2>\n<p>風神録入手しそびれたから来週末にでも友人宅に行ってくるかのぅ。……それにしても凄い男幕だったなぁ。想像以上に安泰そうな気がしたし、こっちの活動ももうちょいゆっくりでもいいかなと思ってみた。</p>\n<h2>ゆとりの ほうそくが みだれる！</h2>\n<p>Web拍手ほど多くは無いもののちょこちょこメールとかも戴いたりしております。ですが、最近Web拍手(もどき)とメールとを履き違えてるのか、ちょっと<strong>ゆとりが過ぎて</strong>グランドワロスしちゃってるものが目に付くんで、以前mixiにも書いたんですが「よいこのみんなは、こんなめーるはおくらないでね！」って意味も含めてちょっと一部掲載してみるテスト。個人特定できそうな部分は伏せてますのであしからず。</p>\n<h2>「たのしかったけど1000円の割にあわなかったので返金してくださいｖ（◇＿◇）」</h2>\n<p>頼むから毎日送らないで1回送信すれば届くんだから orz</p>\n<h2>件名:「キャンセル」 本文:(なし)</h2>\n<p><a href=\"http://e.danmaq.com/\">e-danmaq個人通販</a>の注文に対するメール。差出人アドレスから注文者特定してキャンセル処理したけど、ちょいとその言い方ないんとちゃうのん？しかも自分で注文しておいて。</p>\n<h2>ｍｋｍ氏(弾幕風作者)の掲示板にて「夢終劇の修正パッチまだですか？」</h2>\n<p>お前は一太郎の修正パッチをM$に求めるのかと小一時間（ｒｙ</p>\n<h2>(mixiメッセージ)「友達少ないからマイミクになってあげますｏ」</h2>\n<p>蹴り倒しました。</p>\n<h2>「転売ってどうやってやるんですか？」</h2>\n<p>ノーコメント。</p>\n<h2>「夢終劇をWinnyで落としたいんだけどどこでダウンロードするか分からない。」</h2>\n<p>ノーコメント。</p>\n<h2>「ヤフオクって東方夢終劇売ってますか？」</h2>\n<p>ノーコメント！！！！！</p>\n<h2>「暇なので新作作ってください」</h2>\n<p>少なくとも同人ソフトは催促されて作るものとは違うと思うぜ？</p>\n<h2>(要約)「あなたの作ったゲームに圭一(仮名)が夢中になって、私の言うことも聞かず目が悪くなってしまいます。責任とってくれるのですか！？」</h2>\n<p>それそのまま同じことスクエニや癌にもメールしたんだろうな？(^ω^)</p>\n<h2>極めつけ</h2>\n<p>Eメールじゃなくてリアルメールなんだが、突然夢終劇が返送されてきた。普通郵便の差出人なし。開けたら緩衝材なし、ケースに軽くヒビ、開封済みでキャラメル包装とオビなし。さらには書面らしき書面もなし。正直？＿＿＿＿？CD不良かとも思ったが普通に読み込めたし一体なんなんだろう…</p>\n<p>まだ大量のゆとりが残ってるけど後は殆ど上記の繰り返しだったり、晒すほどのインパクトはなかったりとかなんでとりあえずこの辺で。</p>\n<h2>よいこのみんなは、こんなめーるはおくらないでね！</h2>\n<p>あとケータイtoケータイのメールじゃあるまいし、件名くらい横着せんと書こうぜ 'Ａ｀</p>","frontmatter":{"cover":null,"date":"2007-05-27T14:09:00+09:00","strDate":"2007/5/27","title":"らむたん"}}},"pathContext":{"path":"/ja/blog/2007/05/27-らむたん/","langKey":"ja"}}

